<template>
  <div class="channel">
    <van-grid :column-num="2" class="cardList" square :gutter="0" :border="false" v-if="itemList">
      <van-grid-item v-for="(item,index) in itemList.data.data" :key="index" class="card">
        <img :src="item.icon" />
        <div>{{item.catename}}</div>
      </van-grid-item>
    </van-grid>
  </div>
</template>
<script>
import Vue from "vue";
import { Grid, GridItem } from "vant";
Vue.use(Grid);
Vue.use(GridItem);
export default {
  data() {
    return {
      itemList: Object
    };
  },
  created() {
    this.geturl();
      
  },
  mounted() {
    // this.geturl();
  },
  methods: {
    geturl: function() {
      window.axios
        .get(
          "https://api.kele8.cn/agent/https://app.vmovier.com/apiv3/cate/getList"
        )
        .then(res => {
          this.itemList = res;
        })
        .catch(error => {
          console.log(error);
        });
    }
  }
};
</script>
<style lang="less" scoped>
.channel {
  .cardList {
    width: 100%;
    padding: 0px;
    .card {
      padding: 0;
      position: relative;
      img {
        width: 100% !important;
        height: 100% !important;
      }
      div{
          position: absolute;
          top: 40%;
          left: 40%;
          color: white;
          font-weight: 700;
          font-size: 26px;
      }
    }
  }
}
</style>